import { createRouter, createWebHistory, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginView from '../views/LoginView.vue'
import PageNotFound from '../views/PageNotFound.vue'
import Logout from '../views/Logout'
import PaymentInputView from '../views/PaymentInputView.vue'
import InventoryView from '../views/InventoryView.vue'
import testview from '@/views/testview.vue'
import AdjustmentsView from '@/views/AdjustmentsView.vue'
import ReceivingReportView from '@/views/ReceivingReportView.vue'
import SalesView from '@/views/SalesView.vue'
import RRList from '@/views/RRList.vue'
import ADList from '@/views/ADList.vue'
import SIList from '@/views/SIList.vue'
import CustomersView from '@/views/CustomersView.vue'
import SuppliersView from '@/views/SuppliersView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      requiresLogin: true
    },
    transition: 'slide'
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView,
    //meta: {
    //  requiresLogin: true
    //}
  },
  {
    path: '/logout',
    name: 'logout',
    component: Logout,
  },
  {
    path: '/:pathMatch(.*)*',
    component: PageNotFound
  },
  {
    path: '/payment-input',
    name: 'payment-input',
    meta: {
      requiresLogin: true
    },
    component: PaymentInputView
  },
  {
    path: '/inventory',
    name: 'inventory',
    meta: {
      requiresLogin: true
    },
    component: InventoryView,
  },
  {
    path: '/test',
    name: 'test',
    meta: {
      requiresLogin: true
    },
    component: testview,
  },
  {
    path: '/adjustments',
    name: 'adjustments',
    meta: {
      requiresLogin: true
    },
    component: AdjustmentsView
  },
  {
    path: '/receive',
    name: 'receive',
    meta: {
      requiresLogin: true
    },
    component: ReceivingReportView
  },
  {
    path: '/sales',
    name: 'sales',
    meta: {
      requiresLogin: true
    },
    component: SalesView
  },
  {
    path: '/rr-list',
    name: 'rr-list',
    meta: {
      requiresLogin: true
    },
    component: RRList
  },
  {
    path: '/ad-list',
    name: 'ad-list',
    meta: {
      requiresLogin: true,
    },
    component: ADList
  },
  {
    path: '/si-list',
    name: "si-list",
    meta: {
      requiresLogin: true
    },
    component: SIList
  },
  {
    path: '/suppliers',
    name: 'suppliers',
    meta: {
      requiresLogin: true
    },
    component: SuppliersView
  },
  {
    path: '/customers',
    name: 'customers',
    meta: {
      requiresLogin: true
    },
    component: CustomersView
  }

  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // }
]

const router = createRouter({
  history: process.env.IS_ELECTRON ? createWebHashHistory() : createWebHistory(),
  routes
})

export default router
